"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Plus, Clock, Edit, Trash2 } from "lucide-react"

interface DeliverySlot {
  id: string
  name: string
  startTime: string
  endTime: string
  maxOrders: number
  currentOrders: number
  isActive: boolean
  days: string[]
}

const mockSlots: DeliverySlot[] = [
  {
    id: "1",
    name: "Early Morning",
    startTime: "06:00",
    endTime: "08:00",
    maxOrders: 50,
    currentOrders: 23,
    isActive: true,
    days: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
  },
  {
    id: "2",
    name: "Morning",
    startTime: "08:00",
    endTime: "10:00",
    maxOrders: 75,
    currentOrders: 45,
    isActive: true,
    days: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
  },
  {
    id: "3",
    name: "Late Morning",
    startTime: "10:00",
    endTime: "12:00",
    maxOrders: 100,
    currentOrders: 78,
    isActive: true,
    days: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
  },
  {
    id: "4",
    name: "Afternoon",
    startTime: "14:00",
    endTime: "16:00",
    maxOrders: 80,
    currentOrders: 52,
    isActive: true,
    days: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
  },
  {
    id: "5",
    name: "Evening",
    startTime: "16:00",
    endTime: "18:00",
    maxOrders: 90,
    currentOrders: 67,
    isActive: true,
    days: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
  },
  {
    id: "6",
    name: "Night",
    startTime: "18:00",
    endTime: "20:00",
    maxOrders: 60,
    currentOrders: 34,
    isActive: false,
    days: ["Fri", "Sat", "Sun"],
  },
]

const allDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]

export default function DeliverySlotsPage() {
  const [addSlotOpen, setAddSlotOpen] = useState(false)
  const [selectedDays, setSelectedDays] = useState<string[]>(allDays)

  const toggleDay = (day: string) => {
    setSelectedDays((prev) => (prev.includes(day) ? prev.filter((d) => d !== day) : [...prev, day]))
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">Delivery Slots</h1>
          <p className="text-muted-foreground">Configure delivery time slots and availability</p>
        </div>
        <Dialog open={addSlotOpen} onOpenChange={setAddSlotOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="mr-2 h-4 w-4" />
              Add Slot
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-md">
            <DialogHeader>
              <DialogTitle>Add Delivery Slot</DialogTitle>
              <DialogDescription>Create a new delivery time slot</DialogDescription>
            </DialogHeader>
            <form className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="slotName">Slot Name</Label>
                <Input id="slotName" placeholder="e.g., Morning Slot" />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="startTime">Start Time</Label>
                  <Input id="startTime" type="time" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="endTime">End Time</Label>
                  <Input id="endTime" type="time" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="maxOrders">Max Orders</Label>
                <Input id="maxOrders" type="number" placeholder="50" />
              </div>
              <div className="space-y-2">
                <Label>Available Days</Label>
                <div className="flex flex-wrap gap-2">
                  {allDays.map((day) => (
                    <Button
                      key={day}
                      type="button"
                      variant={selectedDays.includes(day) ? "default" : "outline"}
                      size="sm"
                      onClick={() => toggleDay(day)}
                    >
                      {day}
                    </Button>
                  ))}
                </div>
              </div>
              <div className="flex justify-end gap-2">
                <Button type="button" variant="outline" onClick={() => setAddSlotOpen(false)}>
                  Cancel
                </Button>
                <Button type="submit">Add Slot</Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Slots Grid */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        {mockSlots.map((slot) => (
          <Card key={slot.id} className={!slot.isActive ? "opacity-60" : ""}>
            <CardHeader className="pb-3">
              <div className="flex items-start justify-between">
                <div className="flex items-center gap-2">
                  <div className="p-2 rounded-lg bg-primary/10">
                    <Clock className="h-5 w-5 text-primary" />
                  </div>
                  <div>
                    <CardTitle className="text-lg">{slot.name}</CardTitle>
                    <CardDescription>
                      {slot.startTime} - {slot.endTime}
                    </CardDescription>
                  </div>
                </div>
                <Switch checked={slot.isActive} />
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">Orders</span>
                <span className="font-medium">
                  {slot.currentOrders} / {slot.maxOrders}
                </span>
              </div>
              <div className="w-full h-2 bg-secondary rounded-full">
                <div
                  className={`h-full rounded-full ${
                    slot.currentOrders / slot.maxOrders > 0.8
                      ? "bg-destructive"
                      : slot.currentOrders / slot.maxOrders > 0.5
                        ? "bg-warning"
                        : "bg-success"
                  }`}
                  style={{ width: `${(slot.currentOrders / slot.maxOrders) * 100}%` }}
                />
              </div>
              <div className="flex flex-wrap gap-1">
                {slot.days.map((day) => (
                  <Badge key={day} variant="outline" className="text-xs">
                    {day}
                  </Badge>
                ))}
              </div>
              <div className="flex justify-end gap-2">
                <Button variant="ghost" size="sm">
                  <Edit className="h-4 w-4" />
                </Button>
                <Button variant="ghost" size="sm" className="text-destructive">
                  <Trash2 className="h-4 w-4" />
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
